import react from "react";
export default class Counter extends react.Component {
  constructor() {
    super();
    this.state = { val: 0, cstate: "Start Counter" };
  }
  counterStarts = () => {
    console.log("this");
    var vala = this.state.cstate;
    var timer = () => {
      this.setState((prevState) => {
        return {
          val: prevState.val + 1
        };
      });
    };
    setInterval(timer, 1000);
    if (vala === "Start Counter") {
      this.setState({
        cstate: "Stop Counter"
      });
    } else {
      this.setState({
        cstate: "Start Counter"
      });
    }
  };
  render() {
    return (
      <>
        <div className="counter-shell" style={styles.counter_shell}>
          <h1 style={styles.style}>
            <br />
            Counter:{this.state.val > 10 ? 10 : this.state.val}
          </h1>
          <br />
          <h1 style={styles.style}>
            <br />
            Counter-2:{this.state.val}
          </h1>
          <button
            style={styles.button}
            id="button-counter"
            className="button-counter"
            onClick={() => this.counterStarts()}
          >
            {this.state.cstate}
          </button>
        </div>
      </>
    );
  }
}
const styles = {
  counter_shell: {
    height: "400px",
    width: "300px",
    border: "4px solid teal",
    margin: "20px auto"
  },
  style: {
    color: "teal"
  },
  button: {
    color: "white",
    backgroundColor: "teal",
    padding: "10px",
    margin: "40px",
    fontWeight: "900",
    cursor: "pointer"
  }
};
